.contentcontainer {
    display: flex;
    align-items: center;
    flex-direction: column;
    position: absolute;
    top: 25px;
    width: 100%;
}

.infotextlink {
    text-decoration: none;
    color: #654DFF;
}

.topbar {
    width: 100%;
    height: 70px;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 0 4%;
    box-sizing: border-box;
    flex-wrap: wrap;
}

.topbarleftalign {
    display: flex;
    align-items: center;
    height: 100%;
}

.logo {
    background-image: url(./images/logo.svg);
    height: 50px;
    width: 282px;
    background-size: cover;
}

.logostaking {
    font-weight: 800;
    font-size: 30px;
    user-select: none;
}

.connectwalletbutton {
    height: 50px;
    background-color: #113CFC;
    display: flex;
    justify-content: center;
    align-items: center;
    color: white;
    cursor: pointer;
    padding: 0 25px;
    font-weight: 700;
    border-radius: 12px;
    position: absolute;
    top: 0px;
    right: 20px;
}

.disabledharvest {
    background-color: #636363 !important;
}

.connectwalletbuttonactive {
    background-color: #7DEBA2;
    color: white;
}

.infotext {
    margin-top: 4%;
    font-weight: 800;
    text-align: center;
    font-size: 20px;
}

.stakecard {
    width: 600px;
    background-color: white;
    margin-top: 50px;
    box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
    border-radius: 14px;
    display: flex;
    flex-direction: column;
    align-items: center;
    padding-bottom: 30px;
}

.stakecardheading {
    font-size: 22px;
    font-weight: 800;
}

.lpicon {
    background-image: url(./images/lpicon.svg);
    width: 118px;
    height: 39px;
    background-size: contain;
    margin-bottom: 20px;
}

.inforow {
    width: 80%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin: 6px 0;
}

.inforow * {
    font-size: 17px;
    font-weight: 800;
    margin: 0;
}

.infoundertext {
    font-weight: 700;
    font-size: 15px;
}

.extramargin {
    margin-top: 20px;
}

.harvestbutton {
    background-color: #113CFC;
    display: flex;
    justify-content: center;
    align-items: center;
    color: white;
    cursor: pointer;
    padding: 14px 18px;
    line-height: 100%;
    border-radius: 13px;
    user-select: none;
}

.addremovebuttoncontainer {
    display: flex;
}

.addremovebutton {
    cursor: pointer;
    user-select: none;
    width: 45px;
    height: 45px;
    margin: 5px;
    background-color: #113CFC;
    border-radius: 10px;
    color: white;
    display: flex;
    justify-content: center;
    align-items: center;
    line-height: 80%;
}

@media only screen and (max-width: 980px) {
    .topbar{
        justify-content: space-between;
    }
    .connectwalletbutton{
        position: relative;
    }
}

@media only screen and (max-width: 830px) {
    .logostaking{
        display: none;
    }
}

@media only screen and (max-width: 730px) {
    .stakecard{
        width: 90%;
    }
}

@media only screen and (max-width: 485px) {
    .topbar{
        justify-content: center;
        margin-bottom: 100px;
    }
}