.bodycontainer{
    width: 100%;
    height: 100%;
    position: fixed;
    top: 0;
    background-color: rgba(0, 0, 0, 0.6);
}

.body {
    width: 500px;
    background-color: white;
    position: fixed;
    top: 50%;
    right: 50%;
    transform: translate(50%, -50%);
    display: flex;
    flex-direction: column;
    align-items: center;
    box-shadow: 0px 0px 7px 5px rgba(17, 60, 252, 0.25);
    border-radius: 20px;
}

.heading {
    font-size: 18px;
    font-weight: 800;
}

.numbercontainer{
    display: flex;
    width: 80%;
    align-items: flex-end;
    flex-direction: column;
}

.available{
    font-size: 15px;
    font-weight: 600;
    margin: 10px;
}

.input{
    width: 100%;
    height: 45px;
    border: 2px solid black;
    border-radius: 9px;
    box-sizing: border-box;
    outline: none;
    font-size: 22px;
    font-weight: 600;
    padding: 0 130px 0 10px;
}

.inputcontainer{
    width: 100%;
    position: relative;
}

.cakehint{
    position: absolute;
    right: 60px;
    top: 13px;
    color: #808080;
    margin: 0;
    font-weight: 600;
    user-select: none;
}

.maxbutton{
    position: absolute;
    right: 15px;
    top: 13px;
    margin: 0;
    color: #113CFC;
    font-weight: 700;
    cursor: pointer;
}

.buttonpair{
    margin-top: 30px;
    width: 70%;
    display: flex;
    justify-content: space-between;
    margin-bottom: 20px;
}

.button{
    font-weight: 600;
    width: 110px;
    height: 40px;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 9px;
    cursor: pointer;
    line-height: 100%;
    background-color: #113CFC;
    color: white;
    box-sizing: border-box;
}

.cancelbutton:hover{
    background-color: #113CFC;
    color: white;
}

.cancelbutton{
    transition: 0.2s;
    background-color: white;
    border: 1px solid black;
    color: black;
}

.typechooser{
    display: flex;
    width: 90%;
    flex-direction: column;
    align-items: center;
    margin-top: 30px;
}

.typechoosertype{
    margin: 2px 0;
    padding: 5px;
    border: 2px solid black;
    cursor: pointer;
    border-radius: 6px;
    font-weight: 600;
}

.selectedtype{
    border: 3px solid #113CFC;
}

.warningmessage{
    color: rgb(243, 73, 73);
    text-align: center;
}

@media only screen and (max-width: 530px) {
    .body{
        width: 90%;
    }
}